import { handleActions } from 'redux-actions';
import actions from './actions';

const {
  getTeamInfo, // 
  uploadApplication, // 
  createTeam, // 

  getUserList, // userlist api
  openManagerModal, // 
  closeManagerModal, // 
  userToAdmin, // 
  adminToUser, // 
  openRemoveModal, // 
  closeRemoveModal, // 
  removeUser, // 
  changePage, // 

  openUpgradeModal, // 
  closeUpgradeModal, // 

  openTransferModal, // 
  closeTransferModal, // 
  transferTeam, // 

  openDismissModal, // 
  closeDismissModal, // 
  dismissTeam, // 

  openExitModal, // 
  closeExitModal, // 
  exitTeam, // 

  getAllApps, //  
} = actions;

const defaultState = {
  teamData: {}, //   
  managerModal: false, //  
  removeModal: false, //  
  upgradeModal: false, //  
  transferModal: false, //  
  dismissModal: false, //  
  exitModal: false, //  
  applicationlist: [], //  
  userList: {}, //  
  activePage: 1, //  
  exitTeamMsg: '',
  dismissTeamMsg: '',
};


const reducer = handleActions({
  //
  [getTeamInfo]: (state, { payload, error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
      teamData: payload,
    };
  },
  [uploadApplication]: state => state,
  [createTeam]: (state, { payload, error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
      teamData: payload,
    };
  },
  [getUserList]: (state, { payload, error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
      userList: payload,
    };
  },
  [openManagerModal]: state => ({
    ...state,
    managerModal: true,
  }),
  [closeManagerModal]: state => ({
    ...state,
    managerModal: false,
  }),
  [userToAdmin]: state => state,
  [adminToUser]: state => state,

  [openRemoveModal]: state => ({
    ...state,
    removeModal: true,
  }),
  [closeRemoveModal]: state => ({
    ...state,
    removeModal: false,
  }),
  [removeUser]: (state, { error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
    };
  },
  [changePage]: (state, { payload: eventKey }) => ({
    ...state,
    activePage: eventKey,
  }),

  [openUpgradeModal]: state => ({
    ...state,
    upgradeModal: true,
  }),
  [closeUpgradeModal]: state => ({
    ...state,
    upgradeModal: false,
  }),

  [openTransferModal]: state => ({
    ...state,
    transferModal: true,
  }),
  [closeTransferModal]: state => ({
    ...state,
    transferModal: false,
  }),
  [transferTeam]: (state, { error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
    };
  },

  [openDismissModal]: state => ({
    ...state,
    dismissModal: true,
  }),
  [closeDismissModal]: state => ({
    ...state,
    dismissModal: false,
  }),
  [dismissTeam]: (state, { payload, error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
      dismissTeamMsg: payload,
    };
  },

  [openExitModal]: state => ({
    ...state,
    exitModal: true,
  }),
  [closeExitModal]: state => ({
    ...state,
    exitModal: false,
  }),
  [exitTeam]: (state, { payload, error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
      exitTeamMsg: payload,
    };
  },
  [getAllApps]: (state, { payload, error }) => {
    if (error) {
      return state;
    }
    return {
      ...state,
      applicationlist: payload,
    };
  },
}, defaultState);

export default reducer;



